.Hangman {
  width: 400px;
  color: black;
  text-align: center;
  zoom: 1.3;
}

.Hangman img {
  margin-left: 90px;
  border: 10px solid white;
  -webkit-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
          box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
}

.Hangman h1 {
  font-size: 4rem;
  font-weight: 100;
  margin-bottom: 0.1em;
  margin-top: 0;
}

.Hangman-img-wrong{
  display: -ms-flexbox;
  display: flex;
 -ms-flex-align: center;
     align-items: center;
 width: 500px;
}


.Hangman-word {
  letter-spacing: 1em;
  margin: 0.4em -1em 0.2em 0;
  font-size: 2rem;
}

.Hangman-wrong{
  margin-left: 40px;
}
.Hangman-btns {
  text-align: center;
  display: inline-block;
  width: 250px;
  margin-top: 0;
}

.Hangman .butn{
  border: none;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
  width: 30px;
  margin: 5px 2px 2px 2px;
  
 
  background: #ffc107;
  color: #fff;
  -webkit-box-shadow: 0 6px #ff9800;
          box-shadow: 0 6px #ff9800;
  border-radius: 5px;
  padding-top: 4px;
}

.Hangman button:hover {
  background-color: #ff9800;
}

.Hangman button:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  -webkit-box-shadow: 0 6px #9e9e9e;
          box-shadow: 0 6px #9e9e9e;
}

.restart{
  width: 150px;
  font-size: 1em;
  border: unset;
  color: white;
  background-color: brown;
  cursor: pointer;
}
.restart:hover{
  background-color: black;
}


